<template>
    <div class="raiders">
   <div class='img'><img src="../assets/images/gzt04.jpg" alt=""></div>
      <div class="raiders-main">
        <div class="title">
          <h2>这样玩广州塔（小蛮腰）最划算</h2>
        </div>
        <div class='info'>
          <p>一般本地人对本地景点都兴趣不大，所以住 广州 那么久都没想过去 广州 塔（小蛮腰）看看。但是孩子放寒假没事干，所以就抽风想去，上网搜索了下信息，发现买票不一样感受很不同，于是我选了性价比最高的那种。</p>
          <img src="../assets/images/raiders/raiders01.jpeg" alt="">
          <img src="../assets/images/raiders/raiders02.jpeg" alt="">
          <p>从上面图片可以看到，参观 广州 塔主要是E区（101-111层）及塔顶区（111层以上），E区主要是103A层的中餐厅，105层的 法国 旋转餐厅，106层的自助餐旋转餐厅，107层的白云大厅，108层的星空大厅，109/110的科普大厅。塔顶区主要是450米户外观光平台，450米极速云霄，460米摩天轮，488米户外观光平台。选择不同，感受不同，价格也不一样，只去E区白云星空科普这三个厅150元/人的票，450米的观光平台加极速云霄228元/人，460米的摩天轮套票298元/人，488米一塔倾城则398元/人，票价高的包含票价低的参观区域，就是你买228的票则150的那三个厅也可以参观，你买398元一塔倾城就全部都可以玩和参观。</p>
          <p>我研究了下各个区域及价格，觉得塔顶区的票价太贵，除了户外观景平台吸引我外，那些摩天轮及极速云霄等都不是我的菜，于是打算买E区的票去白云厅及星空厅看看算了，但是据说超多人，有时需要排一个小时队才能上塔，怎么办，我最讨厌人多，也讨厌排队，哈哈，幸好我发现只要去 广州 塔上的103A、105、106层吃东西，就可以免排队，直接坐餐厅的电梯上去，所以毫不犹豫决定去餐厅吃东西。可是，发现，价格不菲，且看了评论说不！好！吃！NO!</p>
          <img src="../assets/images/raiders/raiders03.jpeg" alt="">
          <img src="../assets/images/raiders/raiders04.jpeg" alt="">
          <p>于是我选了到105层 法国 卢斯特吃下午茶的方案，我们2大1小，1米2以下免费，我提早两天在美团网上团购的，388元/2人，其实只是比两人参观E区(参观E区的票都要150元/人）多了88元，但是却买来方便和舒服，太划算了。其实后来我才发现不用美团网买的，你打 广州 塔餐厅部020-89338218提早一天预定也可以，198元/人，且下午茶是三层的，而我们美团的却是两层的。但是无论哪种方法预定都要提早预定，不然不给进的。</p>
          <p>好了，选好方案，出发。我们从 番禺 去，直接坐三号线就可以到，到 广州 塔B出口，一出去就可以看到 广州 塔了。</p>
          <img src="../assets/images/raiders/raiders05.jpeg" alt="">
          <img src="../assets/images/raiders/raiders06.jpeg" alt="">
          <p>走进门就可以看到餐厅接待处，直接去接待处报电话号码就可以拿票，在餐厅客户处过安检，简单方便，再看看旁边的游客安检处，人，人，人，马上觉得我的选择是对的！我还是非周末天气不好去的，如果是周末天气好，那人更多更恐怖，看看两条道的对比图。所以建议大家多出48元/人去吃个下午茶吧，舒服太多了。</p>
          <img src="../assets/images/raiders/raiders07.jpeg" alt="">
          <img src="../assets/images/raiders/raiders08.jpeg" alt="">
          <img src="../assets/images/raiders/raiders09.jpeg" alt="">
          <p>一到餐厅就有人问手机尾号，然后查到预定后有专人带领入座。由于平日天气也不太好，所以我们去的这天很少人，只有几桌人，所以很安静很舒服。听着舒服的音乐，看着漂亮的景观，陪着自己爱的人，太舒服太惬意了。</p>
         <img src="../assets/images/raiders/raiders10.jpeg" alt="">
         <p>至于下午茶，只能说不难吃，外观拍照还可以，但口感真的很一般，差点就到难吃的地步了。不过我本就不是为吃而来的，也不怀期待，所以还好啦。</p>
          <img src="../assets/images/raiders/raiders11.jpeg" alt="">
          <img src="../assets/images/raiders/raiders12.jpeg" alt="">
          <p>餐厅旋转一圈大概1个小时，下午茶时间是15:00-17:00的，你不想往上参观（吃下午茶是可以免费参观107-111层的），那可以选择一直在餐厅处，吃完走即可，如果想往上参观，则在旁边楼梯走上去，可以走到107层的白云厅和108星空厅及109的科普厅，白云厅、星空厅看到的景和在旋转餐厅看到的是一样的，只是白云厅就在那层的顶部画了些白云，星空厅就画了星空，且没有任何座位坐，周围都是观光客，真的没太多意思，只是白云星空厅都会有一处给别人拍照，但很多人排队（我们这天去很少人，所以不用怎么排，据说平时要排很久），工作人员会帮你拍，免费拍但要钱买照片，不过你把手机给工作人员他也会帮你拍，只是手机拍的没有他们专业排的好看，但一套6张要368元，果断放弃，看手机拍即可。</p>
        <p>本来我打算把109/111层也参观完，等到看完 广州 的夜景才走的，可惜小孩吵着回家，于是连109层的科普厅也没看就走了，所以科普厅长啥样，对不起，不知道。</p>
        <p>这次的 广州 塔下 午茶观光之旅很舒服，推荐给大家。</p>
        </div>
      </div>
      <div class='footer'>
        <p>网站作者</p>
        <p>姓名：<span class='name'>谭静婵</span> </p>
        <p>中南网络学院学号：<span class='number'>16112549039003</span></p>
      </div>
 </div>
</template>
