import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../pages/Home.vue'
import Raiders from '../pages/Raiders.vue'
import Attractions from '../pages/Attractions.vue'
import SpecialDishes from '../pages/SpecialDishes.vue'

Vue.use(VueRouter)


const router  = new VueRouter({
  routes: [
    {
      path: '*',
      component: Home,
    },
    {
      path: '/intro',
      component: Home,
    },
    {
      path: '/raiders',
      component: Raiders,
    },
    {
      path: '/attractions',
      component: Attractions,
    },
    {
      path: '/specialDishes',
      component: SpecialDishes,
    },

  ]
})

export default router;






