      <div class='home-content-main'>
        <div class="title">
          <h3>广州塔</h3>
        </div>
        <div class="content">
          <div class="content-info">
            <p>广州塔（英语：Canton Tower）又称广州新电视塔，昵称小蛮腰。位于广州市海珠区（艺洲岛）赤岗塔附近，距离珠江南岸125米，与珠江新城、花城广场、海心沙岛隔江相望。广州塔塔身主体高454米，天线桅杆高146米，总高度600米 [1-2]  。是中国第一高塔，世界第二高塔，仅次于东京晴空塔，是国家AAAA级旅游景区</p>
          <p>广州塔塔身168~334.4米处设有“蜘蛛侠栈道”，是世界最高最长的空中漫步云梯 [6]  。塔身422.8米处设有旋转餐厅，是世界最高的旋转餐厅 [7]  。塔身顶部450~454米处设有摩天轮，是世界最高摩天轮 [8]  。天线桅杆455~485米处设有“极速云霄”速降游乐项目，是世界最高的垂直速降游乐项目 [9]  。天线桅杆488米处设有户外摄影观景平台，是世界最高的户外观景平台，超越了迪拜哈利法塔的442米室外观景平台，以及加拿大国家电视塔447米的“天空之盖”的高度 [10]  。</p>
          <img src="../assets/images/gzt01.jpg" alt="">
        
          <p>广州塔隶属广州城投集团，由广州市建筑集团有限公司和上海建工集团负责施工，总建筑面积114054平方米，2009年9月竣工，2010年9月30日正式对外开放，2010年10月1日正式公开售票接待游客 [11]  。广州塔有5个功能区和多种游乐设施，包括户外观景平台、摩天轮、极速云霄游乐项目，有2个观光大厅，有悬空走廊，天梯，4D和3D动感影院，中西美食，会展设施，购物商场及科普展示厅。</p>
               <img src="../assets/images/gzt03.jpg" alt="">
          <p>2018年春节花城广场灯光音乐会于2月13日至3月2日每晚举办 [12]  ，每半小时以广州塔为中心，珠江两岸和新中轴线夜景为背景，联动花城广场现场音乐上演大型城市灯光表演，打造节日视觉盛宴。</p>
        <img src="../assets/images/gzt02.png" alt="">
          </div>
          <div class='content-video'>
            <p>视频介绍</p>
           <div class='video-main'>
             <iframe style="width:100%" height="650" frameborder="0" src="https://v.qq.com/txp/iframe/player.html?vid=p0330iplldn" allowFullScreen="true"></iframe>
