           <div class='attractions-main'>
               <div class="title">
                 <p>旋转餐厅</p>
             </div>
             <div class="content">
               <p>广州塔餐厅位于广州塔106层，423米高的璇玑地中海自助餐厅是世界最高的旋转餐厅，可容纳130~150人就餐，已于2014年获得吉尼斯世界记录的验证，被评为“建筑物中最高的旋转餐厅。餐厅为椭圆形环状设计，约100分钟旋转一圈，恰好以一个就餐时间段作为旋转周期，保证所有位置的宾客都能全方位欣赏广州绚丽的美景 [7]  。其中建有四家餐厅，分别为E区106/F璇玑地中海自助餐厅（世界最高旋转餐厅）、105/F卢特斯法国旋转餐厅、103~103A月娇轩中餐厅和-1/F筷子荟餐厅</p>
             </div>
          </div>  
           <div class='attractions-main'>
               <div class="title">
                 <p>488户外摄影观景平台</p>
             </div>
             <div class="content">
             <p>“云霄488”户外摄影观景平台于2013年获得世界吉尼斯纪录“世界最高户外观景平台”荣誉。 [10]  “云霄488”户外摄影观景平台是游客登塔观光所能达到的最高点，位于广州塔天线桅杆位置的488米处，超越了迪拜哈利法塔的442米室外观景平台，以及加拿大国家电视塔447米的“天空之盖”的高度。该平台共177平方米，能一次性容纳20余人。</p> </div>
          </div>  
           <div class='attractions-main'>
               <div class="title">
                 <p>蜘蛛侠栈道</p>
             </div>
             <div class="content">
               <p>广州塔蜘蛛侠栈道位于广州塔168米~334.4米处，是世界最高最长的空中漫步云梯。1088级阶梯围绕塔身核心筒螺旋向上蜿蜒，全长约1000米，跨越总高度166.4米，每层阶梯地面有3块全透明镂空玻璃地板。栈道起点在33层，终点在62层，登完全程一般需要40分钟到1个小时，最快者可用25分钟</p>
             </div> 
           <div class='attractions-main'>
               <div class="title">
                 <p>摩天轮</p>
             </div>
             <div class="content">
             <p>广州塔摩天轮位于广州塔塔顶450~454米处，是世界最高的摩天轮。与一般竖立的摩天轮不同，广州塔摩天轮不是悬挂在轨道上，而是沿着倾斜的轨道运转。运行一周约为20分钟，游客能够从各个角度观赏广州市容。摩天轮共由16个观光球舱组成，联动回转，临边而置，观光球舱每个直径3.2米，可容纳4~6名乘客。</p> </div>
          </div> 
             </div> 
           <div class='attractions-main'>
               <div class="title">
                 <p>极速云霄</p>
             </div>
             <div class="content">
              <p>广州塔“极速云霄”速降体验是目前世界最高的垂直速降游乐项目，位于广州塔455米~485米天线桅杆的超高空位置，比之前世界最高的拉斯维加斯游乐场300多米高的跳楼机，还要高出150米。塔上有两组速降座椅，不仅有传统的“坐跳”，更有模拟自由落体的“站跳”，短短1秒多钟内体验30米的急坠，既可以凌空俯瞰羊城美景又可以体验高空速降。吉尼斯世界纪录委员会为广州塔“速降体验”新开设“世界最高惊险之旅”项目，广州塔“速降体验”成为首个且唯一获得者。</p>
            </div>  </div> 
          </div> 
